import React from 'react'
import ProfilePic from '../img/undraw_profile.svg'
import Projects from './Projects'
import Colors from './Colors'
import Cards from './Cards'
import IllustrationsAndApproach from './IllustrationAndApproach'
import Charts from './Charts'
import PieChart from './PieChart'

function Dashboard() {
  return (
    <>
      {/* <!-- Content Wrapper --> */}
      <div
        id='content-wrapper'
        className='d-flex flex-column'
      >
        {/* <!-- Main Content --> */}
        <div id='content'>
          {/* <!-- Topbar --> */}
          <nav
            className='navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow'
          >
            <button
              id='sidebarToggleTop'
              class='btn btn-link d-md-none rounded-circle mr-3'
            >
              <i class='fa fa-bars'></i>
            </button>
            {/* <!-- Topbar Search --> */}
            <form className='d-none d-sm-inline-block form-inline mr-auto ml-md-3 my-2 my-md-0 mw-100 navbar-search'>
              <div className='input-group'>
                <input
                  type='text'
                  className='form-control bg-light border-0 small'
                  placeholder='Search for...'
                  aria-label='Search'
                  aria-describedby='basic-addon2'
                />
                <div className='input-group-append'>
                  <button className='btn btn-primary' type='button'>
                    <i className='fas fa-search fa-sm'>Search</i>
                  </button>
                </div>
              </div>
            </form>
            {/* <!-- Topbar Navbar --> */}
            <ul class='navbar-nav ml-auto'>
              {/* <!-- Nav Item - Search Dropdown (Visible Only XS) --> */}
              <li class='nav-item dropdown no-arrow d-sm-none'>
                <a
                  class='nav-link dropdown-toggle'
                  href='#'
                  id='searchDropdown'
                  role='button'
                  data-toggle='dropdown'
                  aria-haspopup='true'
                  aria-expanded='false'
                >
                  <i class='fas fa-search fa-fw'></i>
                </a>
                {/* <!-- Dropdown - Messages --> */}
                <div
                  class='
                    dropdown-menu dropdown-menu-right
                    p-3
                    shadow
                    animated--grow-in
                  '
                  aria-labelledby='searchDropdown'
                >
                  <form class='form-inline mr-auto w-100 navbar-search'>
                    <div class='input-group'>
                      <input
                        type='text'
                        class='form-control bg-light border-0 small'
                        placeholder='Search for...'
                        aria-label='Search'
                        aria-describedby='basic-addon2'
                      />
                      <div class='input-group-append'>
                        <button class='btn btn-primary' type='button'>
                          <i class='fas fa-search fa-sm'></i>
                        </button>
                      </div>
                    </div>
                  </form>
                </div>
              </li>

              {/* <!-- Nav Item - Alerts --> */}
              <li class='nav-item dropdown no-arrow mx-1'>
                <a
                  class='nav-link dropdown-toggle'
                  href='#'
                  id='alertsDropdown'
                  role='button'
                  data-toggle='dropdown'
                  aria-haspopup='true'
                  aria-expanded='false'
                >
                  <i class='fas fa-bell fa-fw'></i>
                  {/* <!-- Counter - Alerts --> */}
                  <span class='badge badge-danger badge-counter'>3+</span>
                </a>
                {/* <!-- Dropdown - Alerts --> */}
                <div
                  class='
                    dropdown-list dropdown-menu dropdown-menu-right
                    shadow
                    animated--grow-in
                  '
                  aria-labelledby='alertsDropdown'
                >
                  <h6 class='dropdown-header'>Alerts Center</h6>
                  <a class='dropdown-item d-flex align-items-center' href='#'>
                    <div class='mr-3'>
                      <div class='icon-circle bg-primary'>
                        <i class='fas fa-file-alt text-white'></i>
                      </div>
                    </div>
                    <div>
                      <div class='small text-gray-500'>December 12, 2019</div>
                      <span class='font-weight-bold'>
                        A new monthly report is ready to download!
                      </span>
                    </div>
                  </a>
                  <a class='dropdown-item d-flex align-items-center' href='#'>
                    <div class='mr-3'>
                      <div class='icon-circle bg-success'>
                        <i class='fas fa-donate text-white'></i>
                      </div>
                    </div>
                    <div>
                      <div class='small text-gray-500'>December 7, 2019</div>
                      $290.29 has been deposited into your account!
                    </div>
                  </a>
                  <a class='dropdown-item d-flex align-items-center' href='#'>
                    <div class='mr-3'>
                      <div class='icon-circle bg-warning'>
                        <i class='fas fa-exclamation-triangle text-white'></i>
                      </div>
                    </div>
                    <div>
                      <div class='small text-gray-500'>December 2, 2019</div>
                      Spending Alert: We've noticed unusually high spending for
                      your account.
                    </div>
                  </a>
                  <a
                    class='dropdown-item text-center small text-gray-500'
                    href='#'
                  >
                    Show All Alerts
                  </a>
                </div>
              </li>

              {/* <!-- Nav Item - Messages --> */}
              <li class='nav-item dropdown no-arrow mx-1'>
                <a
                  class='nav-link dropdown-toggle'
                  href='#'
                  id='messagesDropdown'
                  role='button'
                  data-toggle='dropdown'
                  aria-haspopup='true'
                  aria-expanded='false'
                >
                  <i class='fas fa-envelope fa-fw'></i>
                  {/* <!-- Counter - Messages --> */}
                  <span class='badge badge-danger badge-counter'>7</span>
                </a>
                {/* <!-- Dropdown - Messages --> */}
                <div
                  class='
                    dropdown-list dropdown-menu dropdown-menu-right
                    shadow
                    animated--grow-in
                  '
                  aria-labelledby='messagesDropdown'
                >
                  <h6 class='dropdown-header'>Message Center</h6>
                  <a class='dropdown-item d-flex align-items-center' href='#'>
                    <div class='dropdown-list-image mr-3'>
                      <img
                        class='rounded-circle'
                        src='img/undraw_profile_1.svg'
                        alt='...'
                      />
                      <div class='status-indicator bg-success'></div>
                    </div>
                    <div class='font-weight-bold'>
                      <div class='text-truncate'>
                        Hi there! I am wondering if you can help me with a
                        problem I've been having.
                      </div>
                      <div class='small text-gray-500'>Emily Fowler 路 58m</div>
                    </div>
                  </a>
                  <a class='dropdown-item d-flex align-items-center' href='#'>
                    <div class='dropdown-list-image mr-3'>
                      <img
                        class='rounded-circle'
                        src='img/undraw_profile_2.svg'
                        alt='...'
                      />
                      <div class='status-indicator'></div>
                    </div>
                    <div>
                      <div class='text-truncate'>
                        I have the photos that you ordered last month, how would
                        you like them sent to you?
                      </div>
                      <div class='small text-gray-500'>Jae Chun 路 1d</div>
                    </div>
                  </a>
                  <a class='dropdown-item d-flex align-items-center' href='#'>
                    <div class='dropdown-list-image mr-3'>
                      <img
                        class='rounded-circle'
                        src='img/undraw_profile_3.svg'
                        alt='...'
                      />
                      <div class='status-indicator bg-warning'></div>
                    </div>
                    <div>
                      <div class='text-truncate'>
                        Last month's report looks great, I am very happy with
                        the progress so far, keep up the good work!
                      </div>
                      <div class='small text-gray-500'>Morgan Alvarez 路 2d</div>
                    </div>
                  </a>
                  <a class='dropdown-item d-flex align-items-center' href='#'>
                    <div class='dropdown-list-image mr-3'>
                      <img
                        class='rounded-circle'
                        src='https://source.unsplash.com/Mv9hjnEUHR4/60x60'
                        alt='...'
                      />
                      <div class='status-indicator bg-success'></div>
                    </div>
                    <div>
                      <div class='text-truncate'>
                        Am I a good boy? The reason I ask is because someone
                        told me that people say this to all dogs, even if they
                        aren't good...
                      </div>
                      <div class='small text-gray-500'>
                        Chicken the Dog 路 2w
                      </div>
                    </div>
                  </a>
                  <a
                    class='dropdown-item text-center small text-gray-500'
                    href='#'
                  >
                    Read More Messages
                  </a>
                </div>
              </li>

              <div class='topbar-divider d-none d-sm-block'></div>

              {/* <!-- Nav Item - User Information --> */}
              <li class='nav-item dropdown no-arrow'>
                <a
                  class='nav-link dropdown-toggle'
                  href='#'
                  id='userDropdown'
                  role='button'
                  data-toggle='dropdown'
                  aria-haspopup='true'
                  aria-expanded='false'
                >
                  <span class='mr-2 d-none d-lg-inline text-gray-600 small'>
                    Douglas McGee
                  </span>
                  <img class='img-profile rounded-circle' src={ProfilePic} />
                </a>
                {/* <!-- Dropdown - User Information --> */}
                <div
                  class='
                    dropdown-menu dropdown-menu-right
                    shadow
                    animated--grow-in
                  '
                  aria-labelledby='userDropdown'
                >
                  <a class='dropdown-item' href='#'>
                    <i class='fas fa-user fa-sm fa-fw mr-2 text-gray-400'></i>
                    Profile
                  </a>
                  <a class='dropdown-item' href='#'>
                    <i class='fas fa-cogs fa-sm fa-fw mr-2 text-gray-400'></i>
                    Settings
                  </a>
                  <a class='dropdown-item' href='#'>
                    <i class='fas fa-list fa-sm fa-fw mr-2 text-gray-400'></i>
                    Activity Log
                  </a>
                  <div class='dropdown-divider'></div>
                  <a
                    class='dropdown-item'
                    href='#'
                    data-toggle='modal'
                    data-target='#logoutModal'
                  >
                    <i class='fas fa-sign-out-alt fa-sm fa-fw mr-2 text-gray-400'></i>
                    Logout
                  </a>
                </div>
              </li>
            </ul>
            {/* <!-- Topbar Navbar --> */}
          </nav>
          {/* <!-- End of Topbar --> */}
          {/* <!-- Begin Page Content --> */}
          <div className='container-fluid'>
            {/* <!-- Page Heading --> */}
            <div
              className='d-sm-flex 
align-items-center 
justify-content-between mb-4'
            >
              <h1
                className='h3 mb-0 
text-gray-800'
              >
                Dashboard
              </h1>
              <a
                href='#'
                className='d-none d-sm-inline-block 
btn btn-sm btn-primary shadow-sm'
              >
                <i
                  className='fas fa-download fa-sm 
text-white-50'
                ></i>{' '}
                Generate Report
              </a>
            </div>
            {/* Row 1 - 4 cards */}
            <div className='row'>
              {/* 3 cards */}
              <Cards />
            </div>
            {/* Row 2 - 2 charts */}
            <div className='row'>
              {/* Area Chart*/}
              <div className='col-xl-8 col-lg-7'>
                <div className='card shadow mb-4'>
                  <div
                    className='card-header py-3 
d-flex flex-row align-items-center 
justify-content-between'
                  >
                    <h6
                      className='m-0 
font-weight-bold text-primary'
                    >
                      Earnings Overview
                    </h6>
                  </div>
                  {/*  Card Body */}
                  <div className='card-body'>
                    <div className='chart-area'>
                      <Charts />
                    </div>
                  </div>
                </div>
              </div>
              {/*  Pie Chart */}
              <div className='col-xl-4 col-lg-5'>
                <div className='card shadow mb-4'>
                  <div
                    className='card-header py-3 
d-flex flex-row align-items-center 
justify-content-between'
                  >
                    <h6 className='m-0 font-weight-bold text-primary'>
                      Revenue Sources
                    </h6>
                  </div>
                  {/*  Card Body  */}
                  <div className='card-body'>
                    <div className='chart-pie pt-4 pb-2'>
                      <PieChart />
                    </div>
                    <div
                      class='mt-4 text-center 
small'
                    >
                      <span class='mr-2'>
                        <i
                          class='fas fa-circle 
text-primary'
                        ></i>
                      </span>
                      <span class='mr-2'>
                        <i
                          class='fas fa-circle 
text-success'
                        ></i>
                      </span>
                      <span class='mr-2'>
                        <i
                          class='fas fa-circle 
text-info'
                        ></i>
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            {/* Row 3 - Projects and colors*/}
            <div className='row'>
              <div className='col-lg-6 mb-4'>
                {/* Project Card Example*/}
                <div className='card shadow mb-4'>
                  <div className='card-header py-3'>
                    <h6
                      className='m-0 
font-weight-bold text-primary'
                    >
                      Projects
                    </h6>
                  </div>
                  <div className='card-body'>
                    <Projects />
                  </div>
                </div>
                {/* Color System */}
                <div className='row'>
                  <Colors />
                </div>
              </div>
              <div className='col-lg-6 mb-4'>
                {/* <!-- Illustrations and Approch 
--> */}
                <IllustrationsAndApproach />
              </div>
            </div>
          </div>
          {/* <!-- /.container-fluid --> */}
        </div>
        {/* <!-- End of Main Content --> */}
        {/* <!-- Footer --> */}
        <footer className='sticky-footer bg-white'>
          <div className='container my-auto'>
            <div
              className='copyright text-center 
my-auto'
            >
              <span>Copyright &copy; Your Website 2021</span>
            </div>
          </div>
        </footer>
        {/* <!-- End of Footer --> */}
      </div>
      {/* <!-- End of Content Wrapper --> */}
    </>
  )
}

export default Dashboard
